.field {
  width: 100%;
  max-width: 310px;
  margin-bottom: 20px;
}
.field input {
  background-color: #e6e6e6;
  border: 1px solid transparent;
  padding: 25px 20px;
  font-size: 14px;
  letter-spacing: 0.1rem;
  width: 100%;
  transition: border 200ms;
}
.field input:focus {
  box-shadow: none;
  outline: none;
  border: 1px solid #c99376;
}
.field textarea {
  background-color: #e6e6e6;
  border: 1px solid transparent;
  padding: 25px 20px;
  height: 140px;
  resize: vertical;
  font-size: 16px;
  letter-spacing: 0.1rem;
  width: 100%;
