.btn {
  width: 100%;
  max-width: 310px;
  height: 56px;
  color: #212121;
  font-family: 'Lato', sans-serif;
  font-size: 14px;
  letter-spacing: 0.1rem;
  text-align: center;
  background-color: transparent;
  border: 0;
  appearance: none;
  padding: 0;
  margin: 0;
  perspective: 500px;
}
.btn span {
  display: block;
  width: 100%;
  padding: 17px 20px;
  border: 1px solid #c99376;
  will-change: transform;
  transition: all 500ms;
  transform-style: preserve-3d;
}
.btn span:last-child {
  background-color: #c99376;
  transform: translateY(-28px) rotateX(-86deg);
}
.btn:hover span {
  transform: translateY(-28px) rotateX(87deg);
}
.btn:hover span:last-child {
  transform: rotateX(0deg) translateY(-56px);
}
.btn:focus {
  box-shadow: none;
  outline: none;
}
.btn--inverse {
  color: #f7f7f7;
}
.play-btn {
  color: #f7f7f7;
  font-family: 'Lato', sans-serif;
  letter-spacing: 2px;
  font-size: 14px;
  line-height: 1;
}
.play-btn > div {
  display: inline-block;
  vertical-align: middle;
  margin: 0 5px;
}
.play-btn__center {
  will-change: transform;
  transform: scale(1);
  transition: transform 500ms;
}
.play-btn__left {
  transition: opacity 500ms, transform 500ms;
  opacity: 0;
  transform: translateX(15px);
  will-change: opacity, transform;
}
.play-btn__right {
  transition: opacity 500ms, transform 500ms;
  opacity: 0;
  transform: translateX(-15px);
  will-change: opacity, transform;
}
.play-btn:hover .play-btn__center {
  transform: scale(1.1);
}
.play-btn:hover .play-btn__left,
.play-btn:hover .play-btn__right {
  opacity: 1;
  transform: translateX(0px);
}
.arrow--left {
  transform: rotate(90deg);
  transform-origin: 50% 50%;
}
.arrow--right {
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}
.arrow--up {
  transform: rotate(180deg);
  transform-origin: 50% 50%;
}
.arrow--down {
  transform: rotate(0deg);
  transform-origin: 50% 50%;
}
.arrow--bronze .arrow__tail,
.arrow--bronze .arrow__circle,
.arrow--bronze .arrow__head {
  fill: #c99376;
}
.arrow-wrapper {
  padding: 10px;
