.main-slider {
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}
.main-slider__slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  will-change: transform;
  transition: transform 0ms 500ms;
  z-index: 998;
}
.main-slider__slide--left {
  transform: translateX(-100%);
}
.main-slider__slide--left.active {
  transform: translateX(0%);
  transition: transform 500ms;
  z-index: 999;
}
.main-slider__slide--right {
  transform: translateX(100%);
}
.main-slider__slide--right.active {
  transform: translateX(0%);
  transition: transform 500ms;
  z-index: 999;
}
.main-slider__next {
  position: absolute;
  text-align: center;
  bottom: 0;
  left: 0;
  height: 70px;
  width: 100%;
}
.menu-btn {
  position: relative;
  width: 63px;
  height: 63px;
  overflow: hidden;
  padding-left: 10px;
}
.menu-btn .bar {
  height: 1px;
  background-color: #c99376;
  position: absolute;
  overflow: hidden;
  transition: transform 500ms, width 500ms, opacity 500ms, height 100ms 0ms;
  transform-origin: 50% 50%;
}
.menu-btn .bar:after {
  content: '';
  display: block;
  position: absolute;
  height: 1px;
  width: 50%;
  background-color: #212121;
  top: 0;
  left: 0;
  transition: transform 1000ms;
  transform: translateX(63px);
}
.cross.menu-btn .bar:after {
  transform: translateX(-100%);
}
.cross.menu-btn .bar {
  transition: transform 500ms, width 500ms, opacity 500ms, height 100ms 500ms;
}
.menu-btn .bar-1 {
  top: 23px;
  width: 20px;
}
.menu-btn .bar-1:after {
  transition-delay: 0ms;
}
.open.menu-btn .bar-1 {
  width: 36px;
  transform: translateY(6px);
}
.cross.menu-btn .bar-1 {
  width: 36px;
  transform: translateY(6px) rotate(45deg);
  height: 2px;
}
.menu-btn .bar-2 {
  top: 29px;
  width: 28px;
}
.menu-btn .bar-2:after {
  transition-delay: 100ms;
}
.open.menu-btn .bar-2 {
  opacity: 0;
}
.cross.menu-btn .bar-2 {
  opacity: 0;
}
.menu-btn .bar-3 {
  top: 35px;
  width: 36px;
}
.menu-btn .bar-3:after {
  transition-delay: 200ms;
}
.open.menu-btn .bar-3 {
  transform: translateY(-6px);
}
.cross.menu-btn .bar-3 {
  transform: translateY(-6px) rotate(-45deg);
  height: 2px;
}
.menu-btn:hover .bar:after {
  transform: translateX(-100%);
}
