  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 998;
  transition: opacity 500ms 500ms;
}
.projects-slider__background.active {
  z-index: 999;
  opacity: 1;
  transition: opacity 500ms 0ms;
}
.projects-slider__content {
  margin-left: 8.33333333%;
  position: relative;
  min-height: 150px;
}
.projects-slider__item {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 998;
  opacity: 1;
}
.projects-slider__item.active {
  z-index: 999;
}
.projects-slider__title {
  overflow: hidden;
  position: relative;
}
.active .projects-slider__title > * {
  opacity: 1;
  transform: translateY(0px);
  transition: opacity 500ms 250ms, transform 500ms 250ms;
}
.projects-slider__title > * {
  transition: opacity 500ms 0ms, transform 500ms 0ms;
  opacity: 0;
  transform: translateY(150px);
}
.projects-slider__description {
  overflow: hidden;
  position: relative;
}
.active .projects-slider__description > * {
  opacity: 1;
  transform: translateY(0px);
  transition: opacity 500ms 500ms, transform 500ms 500ms;
}
.projects-slider__description > * {
  transition: opacity 500ms 250ms, transform 500ms 250ms;
  opacity: 0;
  transform: translateY(150px);
}
.projects-slider__nav {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  right: 8.33333333%;
  width: 25px;
  height: 160px;
}
.projects-slider__dots {
  text-align: center;
  height: 55px;
  width: 30px;
}
.projects-slider__dot {
  width: 0;
  height: 0;
  background-color: #c99376;
  padding: 0;
  background-clip: content-box;
  transition: all 500ms;
  margin: auto;
  cursor: pointer;
}
.projects-slider__dot.active {
  width: 25px;
  height: 11px;
  padding: 5px 0;
}
.projects-slider__dot.prev,
.projects-slider__dot.next {
  width: 12.5px;
  height: 11px;
  padding: 5px 0;
}
